  //       nextProps.UserReducer.isScreenShotDataArrived=false;
  //       currentState.faces=nextProps.UserReducer.screenShot.data.faces ? nextProps.UserReducer.screenShot.data.faces : 0;
  //       if(!nextProps.UserReducer.screenShot.data.faces){
